export default {
  data() {
    return {
      userinfo: {
        avatarUrl: '',
        nickName: '',
        mobile: '',
      },
      menuList1: [
        {
          imgUrl: '../../static/img/mine/staypay.png',
          name: '待付款',
          dataType: 'payment'
        }, {
          imgUrl: '../../static/img/mine/stayreceive.png',
          name: '待收货',
          dataType: 'received'
        }, {
          imgUrl: '../../static/img/mine/stayassess.png',
          name: '待评价',
          dataType: 'comment'
        }, {
          imgUrl: '../../static/img/mine/success.png',
          name: '已完成',
          dataType: ''
        }, {
          imgUrl: '../../static/img/mine/cancel.png',
          name: '已取消',
          dataType: ''
        }
      ],                        // 我的订单第一行
      menuList2: [
        {
          imgUrl: '../../static/img/mine/discount.png',
          name: '优惠券'
        }, {
          imgUrl: '../../static/img/mine/sign.png',
          name: '我的签到'
        }, {
          imgUrl: '../../static/img/mine/recording.png',
          name: '最近浏览'
        }, {
          imgUrl: '../../static/img/mine/share.png',
          name: '我的推广'
        }
      ],                        // 我的订单第二行
      tabIndex: 0,              // 默认选中点赞
      tabList: ['点赞', '收藏'], // tab
      shareList: [],            // 收藏文章列表
      goodList: []
    }
  },
  computed: {
    statusBarHeight() {
      return this.$store.state.statusBarHeight
    }
  },
  onLoad() {
    
  },
  onShow() {
    // 获取个人信息
    this.getuserinfo()
    // 获取点赞文章
    this.getArticle()
  },
  watch: {
    tabIndex(val) {
      // 点赞文章列表
      if(val === 0) {
        this.getArticle()
        return
      }
      // 获取收藏商品
      this.getKeepGood()
    }
  },
  methods: {
    // 进入个人信息页面
    goPersonal() {
      uni.navigateTo({
        url: 'personal?userinfo=' + JSON.stringify(this.userinfo)
      })
    },
    // 获取个人用户信息
    getuserinfo() {
      this.$http({
        url: this.$api.getuserinfo,
        cb: (err, res) => {
          if(!err && res.code === 1) {
            this.userinfo = res.data.info
          } else if(res.code === 0) {
            uni.showToast({
              title: res.msg,
              icon: 'none'
            })
          } else {
            uni.showToast({
              title: '个人用户信息获取失败',
              icon: 'none'
            })
          }
        }
        
      })
    },
    
    // 点赞或者收藏
    selectTab(index) {
      this.tabIndex = index
    },
    
    // 获取点赞文章列表
    getArticle() {
      this.$http({
        url: this.$api.mylikearticles,
        method: 'POST',
        cb: (err, res) => {
          if(!err && res.code === 1) {
            this.shareList = res.data.myarticles.data
          } else if(res.code === 0) {
            uni.showToast({
              title: res.msg,
              icon: 'none'
            })
          } else {
            uni.showToast({
              title: '点赞文章获取失败',
              icon: 'none'
            })
          }
        }
      })
    },
    
    // 获取收藏的商品列表
    getKeepGood() {
      this.$http({
        url: this.$api.mycollection,
        method: 'POST',
        cb: (err, res) => {
          if(!err && res.code === 1) {
            
            console.log('成功了收藏', res.data.mygoods.data)
            this.goodList = res.data.mygoods.data
          } else if(res.code === 0) {
            uni.showToast({
              title: res.msg,
              icon: 'none'
            })
          } else {
            uni.showToast({
              title: '收藏商品列表获取失败',
              icon: 'none'
            })
          }
        }
      })
    },
    goChild(index) {
      switch(index) {
        case 0:
          uni.navigateTo({
            url: 'discount'
          })
          break
        case 1:
          uni.navigateTo({
            url: 'sign'
          })
          break
        case 2:
          uni.navigateTo({
            url: 'looks'
          })
          break
        case 3:
          uni.navigateTo({
            url: 'bonus/bonusCenter'
          })
          break
      }
    },
    // 分享详情页
    goShareDetail(id) {
      uni.navigateTo({
        url: '../components/shareInfo?article_id=' + id
      })
    },
    // 订单页
    goOrder(name, type) {
      uni.navigateTo({
        url: '../order/order?name=' + name + '&dataType=' + type
      })
    },
    goSetting() {
      uni.navigateTo({
        url: 'setting?userinfo=' + JSON.stringify(this.userinfo)
      })
    },
    
    // 点赞文章点赞
    clickZan(item, index) {
      let that = this
      let url = this.$api.unLike
      this.$http({
        url: url,
        data: {
          article_id: item.article_id
        },
        cb: (err, res) => {
          if(!err && res.code === 1) {
            uni.showToast({
              title: '取消点赞成功',
              icon: 'none'
            })
            that.shareList.splice(index, 1)
          } else if(res.code === 0) {
            uni.showToast({
              title: res.msg,
              icon: 'none'
            })
          } else {
              uni.showToast({
                title: '取消点赞失败',
                icon: 'none'
              })
            }
          }
      })
    },
  }
}
